@import "../../styles/vm.scss";

.headerLight,
.headerDark {
   grid-column: 1/3;
   display: flex;
   justify-content: space-between;
   align-items: center;
   height: 80px;
   min-height: 80px;
   position: relative;
}

.headerLight {
   background-color: $bgc-light;
}

.headerDark {
   background-color: $bgc-dark;
}

.home {
   @include roboto700;
   margin-left: 3.3rem;
   font-size: 4.2rem;
   color: white;
   cursor: pointer;

   &:hover {
      color: $bgc-light-lighter;
   }
}

.navList {
   display: flex;
   margin-right: 3.3rem;
}

.navListItem {
   @include roboto700;
   font-size: 1.6rem;
   color: white;
   text-decoration: none;
   margin-right: 3.3rem;

   &:hover {
      color: $bgc-light-lighter;
   }
}

.navListItemActive {
   color: $bgc-light-lighter;
}

@media(max-width: $brakepoint) {

   .headerLight,
   .headerDark {
      grid-column: 1/3;
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
   .home {
      font-size: 3rem;
      margin-left: 4vw;
   }
}